import React, { useState } from "react";

const InputElement = () => {

    const [inputText, setInputText] = useState("");

    return (
        <div>
            <input
                onChange={(event) => {
                    console.dir(event);
                    setInputText(event.target.value)
                }}
                placeholder="Placeholder text" />
            {inputText}
        </div>
    )
}

export default InputElement;